body.node-body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to bottom right, #996, #99C); }

[data-node="root"] {
  min-height: 100vh;
  max-width: 10rem;
  margin: 0 auto;
  background-color: #fff; }

* {
  box-sizing: border-box;
  color: #333; }

ul, ol, li, h1, h2, h3, h4, h5, h6, input, button, p, th, td {
  margin: 0;
  padding: 0; }

input, button {
  border: none;
  outline: none;
  color: #333; }

ol, ul, li {
  list-style: none; }

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
  font-size: inherit; }

body a {
  color: #272424; }

img {
  border: none;
  vertical-align: top; }

.clearfix {
  display: inline-block;
  content: '';
  clear: both;
  width: 100%; }

.lf {
  float: left; }

.rt {
  float: right; }

.border-top {
  position: relative; }
  .border-top:after {
    content: '';
    position: absolute;
    background-color: #ddd;
    display: block;
    z-index: 1;
    top: auto;
    right: auto;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    transform-origin: 50% 100%;
    transform: scaleY(0.5); }
